import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { FC, useCallback, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import { components } from 'react-select';
import AsyncSelect from 'react-select/async';
import { Button, UncontrolledPopover } from 'reactstrap';
import { adjustExpressions, ERROR, findAllByExpression, OperationEnum, SearchCriteria, SearchExpression, SearchViewModal, showToast } from 'summer';
import { CLASS_NAME, EmpresaVW } from '../../../models/dtos/ger/empresaVW';
import { GlobalParameter } from '../../../models/dtos/ger/globalParameter';
import { UsuarioEmpresaAtual } from '../../../models/dtos/ger/usuarioEmpresaAtual';
import { salvarUsuarioEmpresaAtual } from '../../../services/ger.service';
import { Reducers } from '../../../store/ducks';
import { GlobalState, setGlobalParameter } from '../../../store/ducks/global.duck';

const HeaderCompany: FC = () => {
  const { currentState, globalParameter } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const dispatch = useDispatch();
  const history = useHistory();

  const [isOpen, setIsOpen] = useState(false);
  const [showModal, setShowModal] = useState(false);
  const [tmpValue, setTmpValue] = useState(null);

  const searchCriteria: SearchCriteria = {
    className: CLASS_NAME,
    fetchType: 'PAGED',
    max: 10,
    sortFieldName: 'codigo',
    start: 0,
  };

  const changeCurrentEmpresa = useCallback(
    (value: EmpresaVW) => {
      const usuarioEmpresaAtual: UsuarioEmpresaAtual = {
        empresaAtual: value,
        id: globalParameter.usuario.id,
      };

      setIsOpen(false);

      salvarUsuarioEmpresaAtual(usuarioEmpresaAtual, {
        errorFunction: mensagem => {
          showToast(mensagem, ERROR);
        },
        thenFunction: result => {
          const updatedGlobalParameter: GlobalParameter = {
            acesso: globalParameter.acesso,
            administradora: globalParameter.administradora,
            empresa: result,
            empresaMCG: result.empresaMCG,
            empresaMCP: result.empresaMCP,
            empresaMFP: result.empresaMFP,
            empresaMLF: result.empresaMLF,
            grupoEmpresaConfig: result.grupoEmpresaRaiz.grupoEmpresaConfig,
            grupoHistorico: result.grupoEmpresa.grupoEmpresaConfig.grupoHistorico,
            grupoPlanoConta: result.grupoEmpresa.grupoEmpresaConfig.grupoPlanoConta,
            // idsGrupoEmpresaPais: globalParameter.idsGrupoEmpresaPais,
            perfil: globalParameter.perfil,
            usuario: globalParameter.usuario,
          };
          dispatch(setGlobalParameter(updatedGlobalParameter));
          setTimeout(() => history.push(`/${currentState.toLowerCase()}/dashboard`), 500);
        },
      });
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    [currentState]
  );

  const loadOptions = (inputValue, callback) => {
    const searchExpressions: SearchExpression[] = [];

    if (inputValue) {
      searchExpressions.push({
        fieldName: 'codigo',
        value: inputValue,
      });

      searchExpressions.push({
        fieldName: 'razaoSocial',
        value: inputValue,
      });
    } else {
      searchExpressions.push({
        fieldName: 'codigo',
        operation: OperationEnum.GREATER_THAN_EQUAL,
        value: globalParameter.empresa.codigo,
      });
    }

    searchCriteria.expressions = adjustExpressions(searchExpressions);
    findAllByExpression(searchCriteria, {
      errorFunction: mensagem => {
        showToast(mensagem, ERROR);
      },
      thenFunction: result => {
        callback(result.content);
      },
    });
  };

  const openEmpresaSearchView = () => setShowModal(true);
  const toggleSearch = () => setIsOpen(p => !p);
  const onFocusOut = () => setIsOpen(false);

  const onHideModal = () => {
    setShowModal(false);
    setTmpValue(null);
  };

  const onSelectModal = value => setTmpValue(value);

  const onConfirmModal = () => {
    if (tmpValue) {
      changeCurrentEmpresa({ ...tmpValue });
    }
    setShowModal(false);
    setTmpValue(null);
  };

  const DropdownIndicator = props => {
    return (
      <components.DropdownIndicator {...props}>
        <button onClick={openEmpresaSearchView} className="header-company-button">
          <FontAwesomeIcon icon={['far', 'building']} size="sm" className="text-primary" />
        </button>
      </components.DropdownIndicator>
    );
  };

  return (
    <>
      {showModal && (
        <SearchViewModal
          modalProps={{ isOpen: true, toggle: onHideModal, size: 'xl', backdrop: 'static' }}
          onSelect={onConfirmModal}
          searchDataGridProps={{
            selectRow: {
              clickToSelect: true,
              hideSelectAll: true,
              mode: 'radio',
              onSelect: onSelectModal,
            },
          }}
          searchViewId="EmpresaSearchView"
        />
      )}
      <Button
        id="settingsPopover"
        color="neutral-success"
        className="bg-neutral-primary text-primary font-size-lg p-0 d-inline-block shadow-none border-0 text-center d-44 rounded position-relative btn-transition-none"
        onClick={toggleSearch}
      >
        <FontAwesomeIcon icon={['far', 'building']} size="lg" />
      </Button>
      <UncontrolledPopover
        target="settingsPopover"
        isOpen={isOpen}
        container="body"
        className="popover-custom-wrapper popover-custom-lg"
        placement="auto"
      >
        <div style={{ minWidth: 414, padding: 20 }}>
          <AsyncSelect
            components={{ DropdownIndicator }}
            cacheOptions={true}
            defaultOptions={true}
            loadOptions={loadOptions}
            onChange={changeCurrentEmpresa}
            menuIsOpen={true}
            placeholder=""
            autoFocus={true}
            onBlur={onFocusOut}
            styles={{
              control: base => ({
                ...base,
                borderRadius: 0,
              }),
            }}
          />
        </div>
      </UncontrolledPopover>
    </>
  );
};

export default HeaderCompany;
